@import "vars";
@import "global";
@import "fonts";
// body {
//     /* Скрываем стандартный курсор */
// }

* {
    cursor: none !important;
}
.questions__counter:focus,
.sentences__counter:focus {
    outline: none; /* Убираем стандартное обрамление */
}

.target {
    border: 2px solid blue; /* Добавляем стиль для активного элемента */
    background-color: #f0f0f0; /* Пример изменения фона */
}
.custom-cursor {
    position: fixed;
    width: 10px; /* Размер точки */
    height: 10px;
    background-color: $text; /* Цвет точки */
    border-radius: 50%; /* Чтобы курсор был в форме круга */
    pointer-events: none; /* Чтобы точка не мешала кликам */
    z-index: 10000; /* Разместить поверх всех элементов */
}
.wrapper {
    cursor: none;
    position: relative;
}
.sentences {
    background-color: $bg-sentence;
    &__category,
    &__counter {
        background-color: #660010;
    }
}
.questions {
    background-color: $bg-qeustion;
    &__category,
    &__counter {
        background-color: #95592e;
    }
}
.questions,
.sentences {
    padding: 150px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px 0;
    &__item {
        display: flex;
        gap: 20px;
    }
    &__category {
        flex: 1 1 50%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__counter {
        flex: 1 1 calc(50% / 3);
        display: flex;
        align-items: center;
        justify-content: center;
    }
    &__category,
    &__counter {
        height: 150px;
        color: $text !important;

        font-size: 54px;
    }

    &__counter {
    }
}
.question__modal {
    background-color: $bg-sentence;
    &-wrapper {
        background-color: #95592e;
    }
    width: 100%;
    height: 100%;
    &-text {
        margin-top: auto;
        font-size: 68px;
    }
}
.sentence__modal {
    background-color: $bg-sentence;
    width: 100%;
    height: 100%;
    &-wrapper {
        background-color: #660010;
    }
    &-title {
        text-align: center;
        margin-top: auto;
        font-size: 70px;
    }
    &-text {
        margin-top: 30px;
        font-size: 46px;
    }
}
.question__modal,
.sentence__modal {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    &-wrapper {
        width: 1400px;
        height: 800px;

        padding: 0 30px;
        display: flex;
        flex-direction: column;
        position: relative;
    }
    &-bottom {
        margin-top: auto;
        margin-bottom: 40px;
        display: flex;
        justify-content: space-between;
    }
    &-close {
        position: absolute;
        right: 5%;
        top: 5%;
        z-index: 1;
        color: $text !important;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 50px;
        height: 50px;
        font-size: 28px;
    }
    &-title {
        text-transform: uppercase;
    }
    &-text {
        text-transform: uppercase;
        // font-size: 68px;
        text-align: center;
    }

    &-category {
        font-size: 24px;
    }

    &-coutner {
        font-size: 28px;
    }
}

.super-question {
}
.super-question {
    background-color: $bg-sentence;
    &__wrapper {
        background-color: #844f22;
        width: 1400px;
        height: 800px;

        padding: 0 30px;
        display: flex;
        flex-direction: column;
        position: relative;
    }
    width: 100%;
    height: 100%;
    &__text {
        margin: auto 0;
        text-align: center;
        font-size: 54px;
    }
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 0;
    left: 0;

    &__bottom {
        display: flex;
        justify-content: space-between;
        margin-top: 30px;
    }

    &__category {
        font-size: 24px;
    }

    &__coutner {
        font-size: 28px;
    }

    &__close {
        position: absolute;
    }

    &__variants {
        margin-top: auto;
        margin-bottom: 50px;
        display: grid;
        align-content: center;
        justify-content: center;
        grid-template-columns: repeat(2, 1fr); /* 2 колонки с равной шириной */
        grid-template-rows: repeat(2, auto); /* 2 строки, высота которых зависит от контента */
        gap: 30px;
    }

    &__variant {
        width: 100%;
        font-size: 40px;
        min-height: 100px;
        padding: 20px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        text-transform: uppercase;
        background-color: #aa7238;
    }
}

