@charset "UTF-8";
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  height: 100%;
}

body {
  font-family: "text", serif;
  padding: 0;
  margin: 0;
  color: #ecc9af;
  max-height: 100vh;
  height: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: 400;
}

form {
  margin: 0;
}

fieldset {
  padding: 0;
  border: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

address {
  font-style: inherit;
}

p {
  margin: 0;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

button {
  display: block;
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

input {
  border: inherit;
  padding: 0;
  margin: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  width: 100%;
  padding: 0 16px 0;
  margin: 0 auto;
}

.visually-hidden {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

.reset-button {
  margin: 0;
  border: 0;
  border: 1px solid black;
  padding: 0;
  font-family: inherit;
  text-align: left;
  color: inherit;
  background-color: transparent;
  outline: none;
  cursor: pointer;
}
.reset-button:disabled {
  cursor: default;
}

@font-face {
  font-family: "text";
  src: url(../fonts/text.woff) format("woff"), url(../fonts/text.woff2) format("woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "logo";
  src: url(../fonts/logo.woff) format("woff"), url(../fonts/logo.woff2) format("woff2");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
* {
  cursor: none !important;
}

.questions__counter:focus,
.sentences__counter:focus {
  outline: none; /* Убираем стандартное обрамление */
}

.target {
  border: 2px solid blue; /* Добавляем стиль для активного элемента */
  background-color: #f0f0f0; /* Пример изменения фона */
}

.custom-cursor {
  position: fixed;
  width: 10px; /* Размер точки */
  height: 10px;
  background-color: #ecc9af; /* Цвет точки */
  border-radius: 50%; /* Чтобы курсор был в форме круга */
  pointer-events: none; /* Чтобы точка не мешала кликам */
  z-index: 10000; /* Разместить поверх всех элементов */
}

.wrapper {
  cursor: none;
  position: relative;
}

.sentences {
  background-color: #3a0506;
}
.sentences__category, .sentences__counter {
  background-color: #660010;
}

.questions {
  background-color: #4d1c08;
}
.questions__category, .questions__counter {
  background-color: #95592e;
}

.questions,
.sentences {
  padding: 150px;
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px 0;
}
.questions__item,
.sentences__item {
  display: flex;
  gap: 20px;
}
.questions__category,
.sentences__category {
  flex: 1 1 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.questions__counter,
.sentences__counter {
  flex: 1 1 16.6666666667%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.questions__category, .questions__counter,
.sentences__category,
.sentences__counter {
  height: 150px;
  color: #ecc9af !important;
  font-size: 54px;
}
.question__modal {
  background-color: #3a0506;
  width: 100%;
  height: 100%;
}
.question__modal-wrapper {
  background-color: #95592e;
}
.question__modal-text {
  margin-top: auto;
  font-size: 68px;
}

.sentence__modal {
  background-color: #3a0506;
  width: 100%;
  height: 100%;
}
.sentence__modal-wrapper {
  background-color: #660010;
}
.sentence__modal-title {
  text-align: center;
  margin-top: auto;
  font-size: 70px;
}
.sentence__modal-text {
  margin-top: 30px;
  font-size: 46px;
}

.question__modal,
.sentence__modal {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}
.question__modal-wrapper,
.sentence__modal-wrapper {
  width: 1400px;
  height: 800px;
  padding: 0 30px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.question__modal-bottom,
.sentence__modal-bottom {
  margin-top: auto;
  margin-bottom: 40px;
  display: flex;
  justify-content: space-between;
}
.question__modal-close,
.sentence__modal-close {
  position: absolute;
  right: 5%;
  top: 5%;
  z-index: 1;
  color: #ecc9af !important;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  font-size: 28px;
}
.question__modal-title,
.sentence__modal-title {
  text-transform: uppercase;
}
.question__modal-text,
.sentence__modal-text {
  text-transform: uppercase;
  text-align: center;
}
.question__modal-category,
.sentence__modal-category {
  font-size: 24px;
}
.question__modal-coutner,
.sentence__modal-coutner {
  font-size: 28px;
}

.super-question {
  background-color: #3a0506;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
}
.super-question__wrapper {
  background-color: #844f22;
  width: 1400px;
  height: 800px;
  padding: 0 30px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.super-question__text {
  margin: auto 0;
  text-align: center;
  font-size: 54px;
}
.super-question__bottom {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.super-question__category {
  font-size: 24px;
}
.super-question__coutner {
  font-size: 28px;
}
.super-question__close {
  position: absolute;
}
.super-question__variants {
  margin-top: auto;
  margin-bottom: 50px;
  display: grid;
  align-content: center;
  justify-content: center;
  grid-template-columns: repeat(2, 1fr); /* 2 колонки с равной шириной */
  grid-template-rows: repeat(2, auto); /* 2 строки, высота которых зависит от контента */
  gap: 30px;
}
.super-question__variant {
  width: 100%;
  font-size: 40px;
  min-height: 100px;
  padding: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-transform: uppercase;
  background-color: #aa7238;
}